.hero {
  position: relative;
}

.hero_no-empty::before {
  content: '';
  width: 100%;
  height: 52px;
  border-top: 3px solid #D8D8D8;
  border-bottom: 3px solid #D8D8D8;
  display: block;
  position: absolute;
  bottom: 23px;
}

.hero__container {
  position: relative;
  max-width: 1827px;
  padding: 0 40px;
}

.hero__slider {
  height: 544px;
}

.hero-slide {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
}

.hero-slide__content {
  text-align: center;
  padding: 0 15px;
}

.hero-slide__title {
  font-size: 48px;
  color: #fff;
  margin-bottom: 12px;
}

.hero-slide__desc {
  font-size: 20px;
  color: #fff;
}

.swiper-pagination {
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  height: 52px;
  z-index: 999;
  border-top: 3px solid #828282;
  border-bottom: 3px solid #828282;
}

.swiper-pagination::before,
.swiper-pagination::after {
  content: '';
  background-color: #828282;
  width: 100%;
  height: 3px;
  display: block;
}

.swiper-pagination::before {
  margin-right: 40px;
}

.swiper-pagination::after {
  margin-left: 40px;
}

.swiper-pagination-bullet {
  width: 39px;
  height: 3px;
  background-color: #828282;
  border-radius: 0;
  flex-shrink: 0;
}


.swiper-pagination-bullet-active {
  background-color: #f49301;
}

.swiper-horizontal>.swiper-pagination-bullets .swiper-pagination-bullet, .swiper-pagination-horizontal.swiper-pagination-bullets .swiper-pagination-bullet {
  margin: 0;
}

.swiper-horizontal>.swiper-pagination-bullets, .swiper-pagination-bullets.swiper-pagination-horizontal, .swiper-pagination-custom, .swiper-pagination-fraction {
  bottom: 23px;
}


.swiper-pagination-bullet:not(:last-child) {
  margin-right: 30px !important;
}

.hero__slider-container .swiper-button-prev::after,
.hero__slider-container .swiper-button-next::after {
  content: '';
}

.hero__slider-container .swiper-button-prev,
.hero__slider-container .swiper-button-next {
  width: 78px;
  height: 78px;
  background: url('../images/arrow-prev.svg') no-repeat;
  opacity: 1;
  top: auto;
  bottom: 9px;
  z-index: 999;
}

.hero__slider-container .swiper-button-prev {
  left: 0;
}

.hero__slider-container .swiper-button-next {
  background-image: url('../images/arrow-next.svg');
  right: 0;
}

