.submenu__link,
.header-main__link,
.header-menu__link,
.submenu-wrapper__link,
.footer-top__link,
.footer-item__link,
.footer-bottom__link {
  text-decoration: underline 0.15em rgba(255, 255, 255, 0);
  transition: text-decoration .5s;
}

.submenu__link:hover,
.header-menu__link:hover,
.header-main__link:hover,
.footer-top__link:hover,
.footer-item__link:hover,
.footer-bottom__link:hover,
.submenu-wrapper__link:hover {
  text-decoration: underline 0.15em currentColor;
}

.submenu__link:hover {
  transition: text-decoration .5s;
}

.header-menu__top {
  display: flex;
  align-items: center;
  gap: 18px;
  margin-top: 57px;
  margin-bottom: 100px;
}

.header-menu__top-item {
  font-size: 14px;
  width: 137px;
  display: block;
}

.header-menu__top-item_main {
  font-size: 48px;
  font-weight: 700;
  width: auto;
}

.header-menu__body {
  position: fixed;
  max-width: 633px;
  height: 100%;
  top: 0;
  left: 0;
  background-color: #fff;  
  overflow: auto;
  transform: translateX(-100%);
  transition: transform .5s ease;
  z-index: 14;
  opacity: 0;
  visibility: hidden; 
}

.header-menu._active .header-menu__body {
  transform: translateX(0px);
  padding-left: 128px;
  padding-right: 30px;
  margin-left: max(15px, calc((100% - 1747px) / 2));
  opacity: 1;
  visibility: visible;
}

.header-menu__area {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 12;
  width: 100%;
  height: 0;
  background-color: rgba(0, 0, 0, .7);
  opacity: 0;
  visibility: hidden;
  transition: .3s;
  cursor: pointer;
}

.header-menu._active .header-menu__area {
  opacity: 1;
  visibility: visible;
  height: 100vmax;
}

.header-menu__item:not(:last-child) {
  margin-bottom: 20px;
}

.submenu-wrapper {
  display: flex;
  justify-content: space-between;
  cursor: pointer;
}

.submenu-btn {
  background-color: transparent;
}

.header-menu__link,
.submenu__link,
.submenu-wrapper__link,
.submenu-btn__text {
  font-size: 16px;
  font-weight: 500;
  line-height: 1.44;
  text-transform: uppercase;
  color: #111;
}

.submenu__item {
  padding: 15px 0;
}

.header-menu__item:first-child,
.submenu__item:first-child {
  padding-top: 0;
}

.submenu__link {
  transition: color .3s;
}

.submenu-btn__icon {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 22px;
  height: 22px;     
  transition: .3s;
}

.submenu-btn__icon path {
  transition: stroke .3s;
}

.header-menu__submenu-btn {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: transparent;
}

.submenu {
  max-height: 0;
  overflow: hidden;
  opacity: 0;
  transition: .3s linear;
}

.has-submenu._open .submenu {
  margin-top: 20px;
  margin-left: 20px;
  opacity: 1;
}

.has-submenu._open .submenu-btn__icon {
  transform: rotate(180deg);
}

