  margin-bottom: 40px;
}

.content-page__content a:not(.tab) {
  font-weight: 500;
  color: #2e0Ca6;
  text-decoration: underline 0.15em rgba(255, 255, 255, 0);
  transition: text-decoration .5s;
}


.news-page__bottom {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 10px;
}

.gallery__images-block {
  position: relative; 
  width: 100%;
  max-width: 1055px;
  height: 100%;
  max-height: 592px;
  margin: 0 auto;
  cursor: pointer;
}

.gallery__images {
  display: flex;
  justify-content: center;
  width: 100%;
  height: 0;
  padding-top: calc(100% / 16 * 9);
  background-color: rgb(74, 39, 201, .1);
  position: relative;
  z-index: 9;
}

.gallery__images-slider-container {
  width: 100%;
  overflow: hidden;
}

.gallery__images-slider {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
}

.gallery__images-slide {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: -1;
  display: flex;
  align-items: center;
  justify-content: center;
}

.gallery__images-slide img {
  width: 100%;
  height: 100%;
  object-fit: contain;
  object-position: center;
}

.gallery__images-zoom {
  content: '';
  position: absolute;
  width: 42px;
  height: 42px;
  border-radius: 5px;
  background-color: rgba(255,255,255,.9);
  left: 50%;
  top: 50%;
  transform: translate(-50%,-50%);
  opacity: 0;
  transition: all .3s ease;
  z-index: 5;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  pointer-events: none;
}

.gallery__images-zoom svg {
  width: 16px;
  height: 16px;
  opacity: .6;
}

.gallery__images-slider:hover .gallery__images-zoom {
  opacity: 1;
}

.images-slider-bottom {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  background-color: rgba(0,0,0,.3);
  display: flex;
  justify-content: space-between;
  flex-direction: row;
  align-items: center;
  padding: 0 45px 0 0;
  z-index: 99;
}

.slider-arrows {
  display: flex;
}

.slider-arrow {
  width: 78px;
  height: 78px;
  background-color: #4a27c9;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: background-color .3s;
}

.slider-arrow_prev {
  background-color: #4121b3;
}

.slider-arrow:hover {
  background-color: #3b1bb1;
}

.slider-arrow_next svg {
  transform: rotate(-180deg);
}

.slider-amount {
  display: flex;
  align-items: center;
  gap: 12px;
  font-size: 22px;
  color: #fff;
}

.gallery__images-slider-container .swiper-pagination {
  width: 100%;
  border: 0;
  bottom: auto;
  height: 40px;
  display: none;
}

.gallery__images-slider-container .swiper-pagination::before,
.gallery__images-slider-container .swiper-pagination::after {
  content: none;
}

.gallery__images-slider-container .swiper-pagination-bullet {
  flex: 1 1 auto;
  max-width: 39px;
}

.gallery__images-slider-container .swiper-pagination-bullet:not(:last-child) {
  margin-right: 6px !important;
}

.gallery__images-slider-container .swiper-pagination-bullet-active {
  background-color: #4a27c9;
}

.gallery__pictures {
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  background-color: #141316;
  opacity: 0;
  visibility: hidden;
  transition: all .3s ease;
  z-index: 99;
  width: 100vw;
}

.gallery__pictures._active {
  opacity: 1;
  visibility: visible;
}

.gallery__pictures-container {
  max-width: 1395px;
  margin: 0 auto;
}

.actions-pictures {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  position: absolute;
  top: 25px;
  right: 25px;
}

.actions-pictures__action {
  border-radius: 5px;
  width: 42px;
  height: 42px;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  transition: all .3s ease;
  position: relative;
  z-index: 20;
  border: 2px solid rgba(255,255,255,.1);
  background-color: transparent;
}

.actions-pictures__action:before {
  content: '';
  position: absolute;
  background-color: rgba(0,0,0,.5);
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  border-radius: 5px;
  z-index: -1;
}

.actions-pictures__action:hover {
  border-color: rgba(255,255,255,.25);
}

.actions-pictures__action + .actions-pictures__action {
  margin: 0 0 0 15px;
}

.actions-pictures__action svg path {
  transition: all .2s ease;
}

.actions-pictures__action_fullscreen {
  margin: 0 0 0 auto;
}

.actions-pictures__action_fullscreen svg {
  width: 18px;
  height: 18px;
}

.actions-pictures__action_fullscreen svg path:nth-child(2) {
  opacity: 0;
}

.actions-pictures__action_fullscreen._active path:nth-child(1) {
  opacity: 0;
}

.actions-pictures__action_fullscreen._active path:nth-child(2) {
  opacity: 1;
}

.actions-pictures__action_close svg {
  width: 16px;
  height: 16px;
}

.gallery__slider-arrow {
  border-radius: 5px;
  width: 42px;
  height: 42px;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  z-index: 10;
  transition: all .3s ease;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  background-color: #4a27c9;
}

.gallery__slider-arrow_prev {
  left: 25px;
}

.gallery__slider-arrow_next {
  right: 25px;
}

.gallery__slider-arrow:hover {
  background-color: #3b1bb1;
}

.gallery__slider-arrow svg {
  width: 16px;
  height: 16px;
}

.gallery__slider-arrow path {
  stroke: #fff;
  stroke-width: 18;
}

.gallery-slide {
  width: 100vw;
  max-width: 100%;
  height: 100vh;
}

.gallery-slide img {
  width: 100%;
  height: 100%;
  max-height: 100vh;
  object-fit: contain;
}

.gallery__slider .swiper-pagination {
  color: #fff;
  font-size: 15px;
  height: 38px;
  display: flex;
  flex-direction: row;
  align-items: center;
  gap: 5px;
  justify-content: center;
  border-radius: 5px;
  font-weight: 500;
  cursor: default;
  position: absolute;
  bottom: 25px;
  left: 50%;
  transform: translateX(-50%);
  z-index: 10;
  width: 76px;
  background-color: rgba(0,0,0,.5);
  border: 0;
}

.loader {
  display: flex;
  align-items: center;
  gap: 10px;
  font-size: 18px;
}

.loader__spinner {
  width: 24px;
  height: 24px;
  background: url('../images/loader.svg') no-repeat center / contain;
  display: block;
  animation: spinner .75s linear infinite;
}

.all-news {
  margin: 70px 0;
}

.all-news__title {
  margin-bottom: 35px;
}

.all-news__list {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 30px 10px;
}

.all-news__more {
  width: 100%;
  background-color: #ddd;
  color: #555;
  margin-top: 32px;
}

.all-news__more .loader__spinner {
  margin: 0 auto;
}

.all-news__more:hover {
  background-color: #ccc;
}

@keyframes spinner {
  from {
      transform: rotate(0deg);
  } to {
      transform: rotate(360deg);
  }
}

.admin-login {
  margin: 20px 0;
}

.admin-login__content {
  max-width: 480px;
  margin: 0 auto;
}

.admin-title {
  margin-bottom: 30px;
}

.form {
  display: flex;
  flex-direction: column;
}

.form__label {
  display: block;
  margin-bottom: 12px;
}

.form__text {
  margin-bottom: 5px;
  display: block;
}

.form__input {
  width: 100%;
  border: 1px solid #e8ebf1;
  color: #000;
  padding: 6px 12px;
  border-radius: 2px;
  font-size: 18px;
}

.error {
  border: 1px solid red;
}

.accordeon__item {
  border: 1px solid #d5d8ea;
}

.accordeon__item:not(:last-child) {
  margin-bottom: 13px;
}

.accordeon__control {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 36px;
  min-height: 82px;
  background-color: #eceefc;
  padding: 0 38px;
  cursor: pointer;
}

.accordeon__control  p {
  font-size: 20px;
  font-weight: 700;
  color: #2b2b2b;
}

.accordeon__icon {
  display: flex;
}

.accordeon__icon::after {
  content: '';
  background: url('../images/accordeon-icon.svg') no-repeat center / contain;
  width: 32px;
  height: 18px;
  display: block;
  transition: .4s linear;
}

.accordeon__content {
  max-height: 0;
  overflow: hidden;
  opacity: 0;
  transition: .4s linear;
}

.accordeon__item._active .accordeon__content {
  opacity: 1;
  padding: 47px 65px;
  box-sizing: content-box;
}

.accordeon__item._active .accordeon__icon::after {
  transform: rotate(180deg);
}

.tab {
  display: inline-block;
  font-size: 20px;
  font-weight: 700;
  color: #2b2b2b;
  padding: 28px 42px 30px 31px;
}

.tab._active {
  background-color: #eceefc;
  color: #4a27c9;
}

.tabs-content {
  border: 1px solid #d5d8ea;
  padding: 41px 30px 51px;
  display: none;
}

.tabs-content._active {
  display: block;
}

